import React, { useCallback } from 'react';
import { useField } from 'formik';
import { Autocomplete as BaseAutocomplete, } from '@material-ui/lab';
import { TextField } from '@material-ui/core';
const Autocomplete = ({ name, InputProps, multiple, ...props }) => {
    const [field, { touched, error }, { setValue }] = useField(name);
    const handleChange = useCallback((_, value) => setValue(value), [setValue]);
    return (<BaseAutocomplete renderInput={params => (<TextField error={touched && !!error} helperText={error} {...params} {...InputProps}/>)} {...field} {...props} onChange={handleChange} multiple={multiple}/>);
};
export default Autocomplete;
